<script>
import tableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import Upload from "@/components/Upload/Upload.vue";

// 將搜尋內容轉小寫後，篩選符合結果
const toLower = (text) => {
  return text.toString().toLowerCase();
};
const searchByName = (items, term) => {
  if (term) {
    return items.filter((item) => toLower(item.id).includes(toLower(term)));
  }
  return items;
};

export default {
  name: "licenseDatabaseQuery-component",
  components: { tableComponent, Pagination1, Upload },
  data() {
    return {
      search: "", // 搜尋內容
      dialog: false, // 視窗開啟
      form: {
        Begin_Data: "", // 複訓辦理日期
        End_Data: "", // 複訓有效日期
        File: "", // 複訓證照
      },
      rules: {
        Begin_Data: [{ required: true, message: "必填", trigger: "change" }],
        End_Data: [{ required: true, message: "必填", trigger: "change" }],
        File: [{ required: true, message: "必填", trigger: "change" }],
      },

      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "short_Name", label: "廠商", width: "200" },
        { id: "p_Name", label: "姓名", width: "200" },
        { id: "id", label: "身分證字號", width: "200" },
        { id: "license_Type", label: "廠商類別", width: "200" },
        { id: "license_Id", label: "證照號碼", width: "200" },
        { id: "begin_Date", label: "證照有效開始日期", width: "200" },
        { id: "end_Date", label: "證照有效截止日期", width: "200" },
      ],
      copyData: [],
      data: [], // 表格資料 (All)
      tableList: [], // 表格資料 (單頁)
    };
  },
  computed: {
    // 將獲取的資料，做顯示前加工處理
    tableData() {
      let data = this.data;
      data.forEach((e, index) => (e.index = index + 1));
      return data;
    },
  },
  methods: {
    getAllLicenses() {
      this.$store
        .dispatch("getAllLicenses")
        .then((res) => {
          this.copyData = JSON.parse(JSON.stringify(res.data.data));
          this.data = this.copyData;
          this.tableList = this.tableData.slice(0, 10);
        })
        .catch((err) => console.log(err));
    },
    // 篩選搜尋之內容，並呈現表格第一頁
    handleSearch(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.data = searchByName(this.copyData, val);
        this.tableList = this.tableData.slice(0, 10);
        this.$store.state.currentPage = 1;
      }
    },

    // 開啟視窗，並代入相應的資訊
    openDialog(item) {
      this.dialog = true;
      this.form = { ...this.form, ...item };
      this.form.Begin_Data = item.begin_Date;
      this.form.End_Data = item.end_Date;
    },
    cancelDialog() {
      this.$refs.form.clearValidate();
      this.dialog = false;
    },
    fileHandleChange(file) {
      this.form.File = file[0].raw;
      this.$refs.form.clearValidate("File");
    },
    fileHandleRemove(file) {
      this.form.File = file;
      this.$refs.form.validateField("File");
    },
    submitDialogForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let data = {
            RID: this.form.rid,
            Begin_Data: this.form.Begin_Data,
            End_Data: this.form.End_Data,
            File: this.form.File,
          };
          this.addExtendLicense(data);
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    // 送出展延的表單及證照檔案
    addExtendLicense(data) {
      let newFormData = this.changeFormData(data);
      this.$store
        .dispatch("addExtendLicense", newFormData)
        .then((res) => {
          if (res.data.isOK) {
            this.dialog = false;
            this.$message({
              type: "success",
              duration: "5000",
              showClose: true,
              message: "展延成功",
            });

            // 清空表單及檔案，並更新表格的資料 (呈現第一頁)
            this.getAllLicenses();
            this.$store.state.currentPage = 1;
            this.$refs.uploadComponent.$refs.Upload.clearFiles();
          }
        })
        .catch((err) => console.log(err));
    },
    deleteHandler(item) {
      const objData = { RID: item.rid };
      this.$store
        .dispatch("DeleteLicense", objData)
        .then((res) => {
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: "5000",
              showClose: true,
              message: "刪除成功",
            });

            // 更新表格的資料 (呈現第一頁)
            this.getAllLicenses();
            this.$store.state.currentPage = 1;
          }
        })
        .catch((err) => console.log(err));
    },

    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    this.getAllLicenses();
  },
};
</script>

<template src="./licenseDatabaseQuery.html"></template>
